.filtersContainer {
    width: 90vw;
    height: 40%;
    margin: 0 auto;
    background: aquamarine;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}

.valueSection,
.diaposoneSection,
.sortSection {
    width: 30%;
    height: 100%;
    background: rgba(61, 61, 61, 0.53);
    border-radius: 10px;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
}

.filtersContainer h5 {
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    color: white;
}